import React from 'react';
import { Link } from 'react-router-dom';
import s from './pagination.module.scss'

const Pagination = ({ perPage, total, paginate, currentPage}) => {

    const pageNumbers = []

    for (let i = 1; i <= Math.ceil(total / perPage); i++) {
        pageNumbers.push(i)
    }

    return (
        <div className='container row'>
            <div className={`container col l8 offset-l1 ${s.pagination_container}`}>
                <ul className={`pagination ${s.pagination_list}`}>
                <li className="waves-effect"  style={{display: currentPage < 2 ? 'none' : 'inline-block'}}><Link onClick={() => paginate(currentPage - 1)} to="#!"><i className="material-icons">chevron_left</i></Link></li>
                    {pageNumbers.map((num, index) => (
                        <li className="waves-effect" key={index}>
                            <Link onClick={() => paginate(num)} to="#!">{num}</Link>
                        </li>
                    ))}
                <li className="waves-effect" style={{display: currentPage < pageNumbers.length ? 'inline-block' : 'none'}}><Link onClick={() => paginate(currentPage + 1)} to="#!"><i className="material-icons">chevron_right</i></Link></li>


                </ul>
            </div>
        </div>
    );
}

export default Pagination;

//Метод Math.ceil() - округление вверх. Округляет аргумент до ближайшего большего целого.