import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom'
import { PostsPage } from './pages/PostsPage/PostsPage';
import { DetailPage } from './pages/DetailPage/DetailPage';
import { CreatePage } from './pages/CreatePage/CreatePage';
import { AuthPage } from './pages/AuthPage/AuthPage';
import { ErrorPage } from './pages/ErrorPage.js/ErrorPage';
import MainPage from './pages/MainPage/MainPage';
import Resources from './pages/Resources/Resources/Resources';
import CreateResources from './pages/Resources/CreateResources/CreateResources';
import DetailResources from './pages/Resources/DetailResources/DetailResources';
import HelpPage from './pages/HelpPage/HelpPage';

export const useRoutes = isAuth => {
    if (isAuth) {//роуты для человека который зарегистрирован и находится в системе
        return (
            <Switch>
                <Route path='/' exact>
                    <MainPage />
                </Route>
                <Route path='/posts' exact>
                    <PostsPage />
                </Route>
                <Route path='/posts/:id' exact>
                    <DetailPage />
                </Route>
                <Route path='/create' exact>
                    <CreatePage />
                </Route>
                <Route path='/error' exact>
                    <ErrorPage />
                </Route>
                <Route path='/resources' exact>
                    <Resources />
                </Route>
                <Route path='/create-resources' exact>
                    <CreateResources />
                </Route>
                <Route path='/resources/:id' exact>
                    <DetailResources />
                </Route>
                <Redirect to='/error' />
            </Switch>
        )
    }
    return (// роуты для человека не зарегистрированного в системе
        <Switch>
            <Route path='/' exact>
                <MainPage />
            </Route>
            <Route path='/posts' exact>
                <PostsPage />
            </Route>
            <Route path='/project-support' exact>
                <HelpPage />
            </Route>
            <Route path='/posts/:id' exact>
                <DetailPage />
            </Route>
            <Route path='/adminlogin' exact>
                <AuthPage />
            </Route>
            <Route path='/resources' exact>
                <Resources />
            </Route>
            <Route path='/resources/:id' exact>
                <DetailResources />
            </Route>
            <Route path='/error' exact>
                <ErrorPage />
            </Route>
            <Redirect to='/error' />
        </Switch>
    )
}


